  animation-name: wiggle;
  animation-duration: 2.5s;
  animation-timing-function: linear;
  animation-iteration-count: 3;
}

@keyframes wiggle {
  0%, 7% {
    transform: rotateZ(0);
  }

  15% {
    transform: rotateZ(-10deg);
  }

  20% {
    transform: rotateZ(7deg);
  }

  25% {
    transform: rotateZ(-7deg);
  }

  30% {
    transform: rotateZ(3deg);
  }

  35% {
    transform: rotateZ(-2deg);
  }

  40%, 100% {
    transform: rotateZ(0);
  }
}

